import uniRequest from 'uni-request'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
export default {
	data(){
		return{
			searchText:'',
			isFocus:false, 
			speechEngine:'iFly',
			loadingType: 'more', //加载更多状态
			skus:[],
			page:1,
			page_size:10
		}
	},
	components:{uniLoadMore},
	onLoad(option){
		console.log(option.value)
		this.searchText = option.value
		this.getDate('add')
	},
	//加载更多
	onReachBottom(){
		this.page_size = this.page_size + 10
		this.getDate();
	},
	methods:{
		async getDate(type){
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
			}else{
				this.loadingType = 'loading'
			}
			try {
				const response = await uniRequest.get('/skus/search/?text='+this.searchText+'&page='+this.page+'&page_size='+this.page_size);
				console.log(response);
				if(response.data.results.length === 0){
					uni.showToast({
						title: '搜索失败，无此关键字',
						icon: 'none',
						duration: 1000
					});
					this.loadingType = 'nomore'
					this.skus = response.data.results
				}else{
					this.skus = response.data.results
					if(this.skus.length < 10){
						this.loadingType = 'nomore'
					}
					
				}
			} catch (error) {
				console.error(error);
			}
		},
		searchStart() {	//触发搜索
			let _this = this;
			if (_this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
			}else{
				_this.$emit('getSearchText', _this.searchText);
				uni.getStorage({
					key:'search_cache',
					success(res){
						let list = res.data;
						if(list.length > 5){
							for(let item of list){
								if(item == _this.searchText){
									return;
								}
							}
							list.pop();
							list.unshift(_this.searchText);
						}else{
							for(let item of list){
								if(item == _this.searchText){
									return;
								}
							}
							list.unshift(_this.searchText);
						}
						_this.hList = list;
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
					},
					fail() {
						_this.hList = [];
						_this.hList.push(_this.searchText);
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
						_this.$emit('getSearchText', _this.searchText);
					}
				})
			}
		},
		startRecognize() {	//语音输入
			let _this = this;
			let options = {};
			options.engine = _this.speechEngine;
			options.punctuation = false; // 是否需要标点符号 
			options.timeout = 10 * 1000;
			plus.speech.startRecognize(options, function(s) {
				_this.searchText = _this.searchText + s;
			});
		},
		//详情页
		navToDetailPage(item) {
			console.log(item)
			//测试数据没有写id，用title代替
			if(item.object === undefined){
			    let id = item.id;
				uni.navigateTo({
					url: `/pages/product/product?id=`+id
				})
			}else{
				let id = item.object.id;
				uni.navigateTo({
					url: `/pages/product/product?id=`+id
				})
			}
			
		},
	}
}
